#auth-page {
  padding-bottom: 30px;
  background-color: $light-grey;

  a {
    color: $blue; }

  .form-card {
    background-color: transparent;
    box-shadow: none;
    height: 100%;

    .card-header {
      text-align: center;
      color: $black;
      font-weight: 900;
      font-size: 1.125rem;
      border: none;
      margin-bottom: 10px;
      padding: 0; }

    .card-body {
      background-color: #fff;
      border-radius: 15px;
      box-shadow: 0px 3px 10px 0px rgba($grey, 0.05);
      flex: 1;
      height: 100%; } }

  .form-bottom {
    @extend %force-centered;
    flex-direction: column;

    .bmd-form-group {
      padding: 0; }

    .c-button {
      margin-top: 15px;
      padding: 0 30px;
      height: 40px;
      width: 200px; } } }

@media screen and (max-width: 575px) {
  #auth-page {
    .form-card {
      height: auto;
      margin: 15px 0; } } }
